var {ajax} = UIkit.util;

export default {

    mounted() {

        ajax(`assets/uikit/package.json`, {responseType: 'json'}).then(({response}) =>
            setTimeout(() => location.href = `https://github.com/uikit/uikit/releases/download/v${response.version}/uikit-${response.version}.zip`, 100)
        );

    }
}




