var {on} = UIkit.util;

export default {

    data: () => ({
        imgLoaded: false
    }),

    mounted() {

        on(this.$el, 'click', '[data-modal]', e => {

            var {iframe, modal} = this.$refs;

            e.preventDefault();

            var el = e.target;

            if (/iPad|iPhone|iPod/.test(navigator.userAgent) && !window.MSStream) {
                window.open(el.href);
                return;
            }

            iframe.src = '';
            UIkit.modal(modal).show();

            requestAnimationFrame(() => iframe.src = el.href);

        });
    }

}




